import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { useState, useEffect } from "react"
import logo from '../assets/img/logo.svg'




export const NavBar = () => {


  const [activeLink, setActiveLink] = useState('home')
  const [scrolled, setScrolled] = useState(false)

  useEffect(() => {
      const onScroll = () => {
          if(window.scrollY > 50){
              setScrolled(true)
          }else{
              setScrolled(false)
          }
      }

      window.addEventListener("scroll", onScroll);

      return () => window.removeEventListener("scroll", onScroll)
  }, [])

  const onUpdateActiveLink = (value) => {
      setActiveLink(value)
  }



  return (

    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" className={scrolled ? "scrolled":""}>
      <Container>
        <Navbar.Brand href="home">
          <img src={logo} alt="logo" className='logo' />
        </Navbar.Brand>
        <Navbar.Brand href="#home">DevHelper!</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link href="#FrontEnd" className={activeLink === 'FrontEnd' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('FrontEnd')}>FrontEnd</Nav.Link>
            <Nav.Link href="#BackEnd" className={activeLink === 'BackEnd' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('BackEnd')}>BackEnd</Nav.Link>

          </Nav>
  
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

