import Form from 'react-bootstrap/Form';
import { Container } from 'react-bootstrap';
import nodeImg from '../assets/img/node-logo.png'
import postImg from '../assets/img/postG.png'
import gitImg from '../assets/img/git-logoU.png'
import hapiImg from '../assets/img/hapi.png'
import $ from 'jquery'
import { Component} from 'react';
import ProgressBar from 'react-bootstrap/ProgressBar'



class BackEnd extends Component {
    constructor(){
        super()
        this.state = {
            totalCheckbox : 0
        }
    }

    updateTotal(){
        this.setState({
            totalCheckbox : $('input[type=checkbox][id=tecnologiasB]:checked').length
        })
    }

    render(){
        return(

            <section id='BackEnd' className='BackEnd'>
    
                <Container>
                    <h1 className="titleB">BackEnd</h1>
                    <p className='pTitle'>Lorem ipsum dolor sit, consectetur adipiscing elit. Nam non lacus augue. Morbi interdum, sem sit amet rhoncus sagittis, nulla massa suscipit est, facilisis condimentum purus tellus eu lectus. Duis porta facilisis tellus, vitae dictum risus venenatis vel. In sapien augue, tincidunt ac urna vel, feugiat facilisis sapien. Phasellus quis urna quis ligula euismod mattis eu volutpat lorem</p>
                    
                    <Form xs = {12} md={6} xl = {7}>
                        {['checkbox'].map((type) => (
                            <div key={`default-${type}`} className="mb-3 FrontForm divBack" xs = {12} md={6} xl = {7}>
                            <input type="checkbox" id="tecnologiasB" onClick={() => this.updateTotal()}/>
                            <img src={nodeImg} alt="nodejs" className='iconsB'/>
                            
                            
                            <input type="checkbox" id="tecnologiasB" onClick={() => this.updateTotal()}/>
                            <img src={postImg} alt="postgre" className='iconsB'/>
    
    
                            <input type="checkbox" id="tecnologiasB" onClick={() => this.updateTotal()}/>
                            <img src={gitImg} alt="express" className='iconsB'/>

                            <input type="checkbox" id="tecnologiasB" onClick={() => this.updateTotal()}/>
                            <img src={hapiImg} alt="express" className='iconsB'/>
    
                            
    
    
                            </div>
                        ))}
                    </Form>
                    <h1>{`Com estas tecnologias você é ${this.state.totalCheckbox * 25}% capaz de trabalhar como`}</h1>
                    <h1 className='destaqueB'>Desenvolvedor BackEnd</h1>
                    <ProgressBar animated  now={this.state.totalCheckbox * 20} className='m-5'/>
                </Container>
    
                
            </section>
    
            
    
        )
    }
}


export default BackEnd;