import Form from 'react-bootstrap/Form';
import { Container } from 'react-bootstrap';
import reactImg from '../assets/img/React-icon.svg.png'
import jsImg from '../assets/img/js-icon.jpeg'
import htmlImg from '../assets/img/html-icon.png'
import cssImg from '../assets/img/logo-css.png'
import reduxImg from '../assets/img/redux-icon.svg'
import $ from 'jquery'
import { Component} from 'react';
import ProgressBar from 'react-bootstrap/ProgressBar'



class FrontEnd extends Component {
    constructor(){
        super()
        this.state = {
            totalCheckbox : 0
        }
    }

    updateTotal(){
        this.setState({
            totalCheckbox : $('input[type=checkbox][id=tecnologiasF]:checked').length
        })
    }

    render(){
        return(

            <section id='FrontEnd' className='FrontEnd'>
    
                <Container>
                    <h1 className="title">FrontEnd</h1>
                    <p className='pTitle'>Lorem ipsum dolor sit, consectetur adipiscing elit. Nam non lacus augue. Morbi interdum, sem sit amet rhoncus sagittis, nulla massa suscipit est, facilisis condimentum purus tellus eu lectus. Duis porta facilisis tellus, vitae dictum risus venenatis vel. In sapien augue, tincidunt ac urna vel, feugiat facilisis sapien. Phasellus quis urna quis ligula euismod mattis eu volutpat lorem</p>
                    
                    <Form xs = {12} md={6} xl = {7}>
                        {['checkbox'].map((type) => (
                            <div key={`default-${type}`} className="mb-3 FrontForm divFront" xs = {12} md={6} xl = {7}>
                            <input type="checkbox" id="tecnologiasF" onClick={() => this.updateTotal()}/>
                            <img src={jsImg} alt="javascript" className='icons'/>
                            
                            
                            <input type="checkbox" id="tecnologiasF" onClick={() => this.updateTotal()}/>
                            <img src={reactImg} alt="react" className='icons'/>
    
    
                            <input type="checkbox" id="tecnologiasF" onClick={() => this.updateTotal()}/>
                            <img src={htmlImg} alt="html" className='icons'/>
    
    
                            <input type="checkbox" id="tecnologiasF" onClick={() => this.updateTotal()}/>
                            <img src={cssImg} alt="css" className='icons'/>
    
    
                            <input type="checkbox" id="tecnologiasF" onClick={() => this.updateTotal()}/>
                            <img src={reduxImg} alt="redux" className='icons'/>
    
                            
    
    
                            </div>
                        ))}
                    </Form>
                    <h1>{`Com estas tecnologias você é ${this.state.totalCheckbox * 20}% capaz de trabalhar como`}</h1>
                    <h1 className='destaque'>Desenvolvedor FrontEnd</h1>
                    <ProgressBar animated  now={this.state.totalCheckbox * 20} className='m-5'/>
                </Container>
    
                
            </section>
    
            
    
        )
    }
}


export default FrontEnd;